.product{
  border: 1px solid lightgray;
  overflow: hidden;
  margin: 20px;
}

.product__image {
  
  width: 100%;
  height:250px;
}

.product__single {
  border-bottom: 1px solid lightgray;
}

.card {
  transition: all 0.3s ease-out;
}

.card:hover {
  -webkit-transform: scale(1.03);
  -ms-transform: scale(1.03);
  transform: scale(1.03);
  box-shadow: 0 0 11px rgba(33, 33, 33, 0.5);
  position: relative;
  bottom: 2px;
}
